export default {
  data () {
    return {
      menuList: []
    }
  },
  onShow() {
    let that= this
    that.getAgreeMent()
  },
  methods:{
    getAgreeMent() {
      let that= this,
        params= {
          url: that.$api.agreementList
        }
      that.$httpRequest(params).then((res) => {
        that.menuList= res.data.list
      })
    }
  }
}
